form.search-catalog {
    margin-top: 2em;
    border: 2px solid #f0e1b0;
    border-radius: 4px;
    padding: 2em 10%;
}

form.search-catalog label {
    font-size: 24px;
    color: #043927;
    font-feature-settings: "clig", "salt";
    font-weight: 600;
}

form.search-catalog div {}

form.search-catalog .form-control,
form.search-catalog .btn {
    height: calc(2.25rem + 1em);
}

form.search-catalog .btn {
    margin-top: 1em;
    width: 100%;
}

@media screen and (min-width:840px) {

    form.search-catalog div {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
    }
    form.search-catalog .form-control,
    form.search-catalog .btn {
        /*display: inline-flex;*/
        flex: 0 0 75%;
        margin-right: 5%;
        font-size: 14px;
    }
    form.search-catalog .btn {
        flex: 0 0 20%;
        margin-right: 0;
        margin-top: 0;
    }
}

@media screen and (min-width:992px) {
    form.search-catalog .form-control {
        display: inline-flex;
        flex: 0 0 80%;
    }
    form.search-catalog .btn {
        flex: 0 0 15%;
    }
}
